@import "vars";

@mixin screen-lg {
  @media (max-width: #{$screen-lg}) {
    @content;
  }
}

@mixin screen-md {
  @media (max-width: #{$screen-md}) {
    @content;
  }
}

@mixin screen-sm {
  @media (max-width: #{$screen-sm}) {
    @content;
  }
}

@mixin screen-xs {
  @media (max-width: #{$screen-xs}) {
    @content;
  }
}

@mixin screen-lg-min {
  @media (min-width: #{$screen-lg + 1}) {
    @content;
  }
}

@mixin screen-md-min {
  @media (min-width: #{$screen-md + 1}) {
    @content;
  }
}

@mixin screen-sm-min {
  @media (min-width: #{$screen-sm + 1}) {
    @content;
  }
}

@mixin screen-xs-min {
  @media (min-width: #{$screen-xs + 1}) {
    @content;
  }
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin list-default {
  margin: 0;
  padding: 0;
  list-style: none;
}

